.redirect-popup {
  max-width: 768px;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .redirect-popup p {
    padding: 0 10px; }
  .redirect-popup .upper-container p,
  .redirect-popup .upper-container span {
    font-size: 18px;
    font-weight: 600;
    color: #354F53; }
    @media screen and (max-width: 576px) {
      .redirect-popup .upper-container p,
      .redirect-popup .upper-container span {
        font-size: 16px; } }
  .redirect-popup .mid-container {
    margin-top: 10px; }
    .redirect-popup .mid-container .selection {
      margin-top: -20px; }
      @media screen and (max-width: 576px) {
        .redirect-popup .mid-container .selection {
          margin-top: 0; }
          .redirect-popup .mid-container .selection .description {
            font-size: 14px; } }
    .redirect-popup .mid-container .error-string {
      margin-top: 20px;
      font-size: 18px;
      font-weight: 600;
      color: #ff6779; }
  .redirect-popup .button-container {
    display: flex;
    margin-top: 27px;
    justify-content: center; }
    @media screen and (max-width: 576px) {
      .redirect-popup .button-container {
        margin-top: 10px;
        flex-direction: column; } }
    .redirect-popup .button-container button {
      margin: 15px; }
