.header-desc-with-price-container .insurer-logo-container {
  max-width: 120px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .header-desc-with-price-container .insurer-logo-container .company-logo-container {
    display: flex;
    justify-content: center;
    align-items: center; }
    .header-desc-with-price-container .insurer-logo-container .company-logo-container .company-logo {
      max-width: 70px;
      max-height: 70px; }
  @media screen and (max-width: 690px) {
    .header-desc-with-price-container .insurer-logo-container {
      max-width: 100px; } }

.header-desc-with-price-container .insurer-desc-container {
  padding: 22px;
  padding-bottom: 20px; }
  .header-desc-with-price-container .insurer-desc-container .insurer-desc-top .insurer-name {
    font-size: 20px;
    font-weight: bold;
    color: #354F53;
    margin: 0; }
  .header-desc-with-price-container .insurer-desc-container .insurer-desc-top .resp-price {
    display: none; }
  .header-desc-with-price-container .insurer-desc-container .desc {
    font-size: 16px;
    font-weight: 200;
    color: #46737A; }
  @media screen and (max-width: 690px) {
