.header-desc-with-tabview-container .button-sub-text {
  margin-top: 8px;
  font-size: 14px;
  font-weight: 800;
  color: #00D7AF;
  text-align: center; }

.header-desc-with-tabview-container .right-main {
  flex: 1;
  min-width: 66%; }
  .header-desc-with-tabview-container .right-main .top-container {
    padding: 15px;
    border-bottom: solid 1px #C8E0E4;
    display: flex;
    align-items: center; }
    .header-desc-with-tabview-container .right-main .top-container .name-container {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center; }
      .header-desc-with-tabview-container .right-main .top-container .name-container .name-logo {
        display: flex;
        align-items: center; }
  @media screen and (max-width: 575px) {
    .header-desc-with-tabview-container .right-main .company-logo-container {
      justify-content: flex-start;
      margin-right: 10px; } }

.header-desc-with-tabview-container .price-container {
  margin-left: 10px; }
  .header-desc-with-tabview-container .price-container p {
    text-align: center; }
  .header-desc-with-tabview-container .price-container .cost {
    font-size: 44px;
    font-weight: 800;
    color: #354F53; }
  .header-desc-with-tabview-container .price-container .unit {
    font-size: 24px;
    font-weight: normal;
    color: #354F53; }
