.header-desc-with-tabview-container .tag-label {
  margin-bottom: 0; }
  @media screen and (max-width: 767px) {
    .header-desc-with-tabview-container .tag-label {
      margin-top: 0; }
      .header-desc-with-tabview-container .tag-label span,
      .header-desc-with-tabview-container .tag-label p {
        font-size: 12px; } }

.header-desc-with-tabview-container .button-medium-flat-green {
  min-width: 0 !important;
  max-width: 100%;
